.footer {
    background: #000000;
    color: white;
    padding: 50px 0 20px;
    margin-top: 50px;
    position: relative;
    border-top: 2px solid white;
}

.footer-content {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-gap: 40px;
    gap: 40px;
    padding: 0 20px;
}

.footer-section {
    margin-bottom: 20px;
    text-align: center;
}

.footer-section h3 {
    color: white;
    font-size: 1.3rem;
    margin-bottom: 20px;
    text-align: center;
}

.footer-section p {
    color: white;
    line-height: 1.8;
    margin-bottom: 20px;
    text-align: center;
}

.footer-section ul {
    list-style: none;
    padding: 0;
    text-align: center;
}

.footer-section ul li {
    margin-bottom: 12px;
    color: white;
}

.footer-section ul li a {
    color: white;
    text-decoration: none;
    transition: opacity 0.3s ease;
}

.footer-section ul li a:hover {
    opacity: 0.8;
    color: white;
}

.social-links {
    text-align: center;
}

.social-links h4 {
    color: white;
    margin-bottom: 15px;
}

.social-icons {
    display: flex;
    grid-gap: 15px;
    gap: 15px;
    justify-content: center;
}

.social-icons a {
    color: white;
    font-size: 1.5rem;
    transition: opacity 0.3s ease;
}

.social-icons a:hover {
    opacity: 0.8;
    color: white;
}

.footer-bottom {
    text-align: center;
    margin-top: 40px;
    padding-top: 20px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    color: white;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
    .footer-content {
        grid-template-columns: 1fr;
        grid-gap: 30px;
        gap: 30px;
    }

    .footer-section {
        text-align: center;
    }

    .social-icons {
        justify-content: center;
    }
}
/* Basic styles */
@keyframes appear3 {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}
header {
    display: flex;
    /* Enables flexbox layout */
    align-items: center;
    /* Aligns content vertically */
    padding: 1rem 2rem;
    animation: appear3 1.5s linear;
    /* Padding for content within the header */
    min-height: 80vh;
    background-image: url('https://jungleworks.com/wp-content/uploads/2020/07/Untitled-design-52.jpg');
