/* Media Queries for Mobile Responsiveness (adjust breakpoints as needed) */

@media screen and (max-width: 768px) {
    header {
        padding: 1rem;
        min-height: 70vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    /* Stack elements vertically on smaller screens */
    .header-content {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: auto;
        min-height: auto;
        padding: 20px 0;
    }

    .text-container {
        text-align: center;
        width: 100%;
        align-items: center;
        justify-content: center;
        padding: 0 20px;
        grid-gap: 20px;
        gap: 20px;
    }

    .text-container h1 {
        font-size: 2.5rem !important;
        text-align: center;
        margin: 0;
    }

    .text-container h1 span {
        font-size: 2.5rem !important;
        display: block;
        margin-top: 8px;
    }

    .text-container p {
        font-size: 1.1rem !important;
        text-align: center;
        line-height: 1.5;
        margin: 0;
    }
}

@media screen and (max-width: 480px) {
    header {
        min-height: 60vh;
    }

    .text-container h1 {
        font-size: 2rem !important;
    }

    .text-container h1 span {
        font-size: 2rem !important;
    }

    .text-container h3 {
        font-size: 1.3rem !important;
    }

    .text-container p {
        font-size: 1rem !important;
    }
}
