/* Products Section Styles */
.products-section {
  padding: 60px 20px;
}

.products-title {
  text-align: center;
  color: white;
  font-size: 2.5rem;
  margin-bottom: 40px;
}

.products-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  gap: 30px;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.product-card {
  background: white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.product-card img {
  width: 100%;
  height: auto;
  transition: all 0.3s ease;
}

.product-card .hover-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  opacity: 0;
  transition: all 0.3s ease;
  pointer-events: none;
}

.product-card:hover .hover-content {
  opacity: 1;
  pointer-events: auto;
}

.product-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.hover-content h3 {
  margin-bottom: 10px;
  font-size: 1.5rem;
  color: white !important;
}

.hover-content p {
  color: white !important;
  font-size: 1rem;
  line-height: 1.5;
}

@media screen and (max-width: 768px) {
  .products-container {
    display: flex !important;
    flex-direction: column !important;
    grid-gap: 20px !important;
    gap: 20px !important;
    padding: 15px !important;
  }

  .product-card {
    width: 90% !important;
    margin: 10px auto !important;
    max-width: 350px !important;
  }

  .hover-content {
    font-size: 0.9rem;
  }
}

/* Reset any conflicting grid styles */
@media screen and (max-width: 768px) {
  .products-container > * {
    grid-column: auto !important;
  }
}

